import React from 'react'
import {Card,Button,Input, Table,Pagination, Form,Modal} from 'antd'
import UserInfo from './UserInfo'
import './index.css'
const columns = (showDetail) => [
{
  title: '名字',
  dataIndex: 'name',
}, {
  title: '别名',
  dataIndex: 'alias',
}, {
  title: '描述',
  dataIndex: 'descript',
}, {
  title: '用户组',
  dataIndex: 'user_group_name',
}, {
  title: '操作',
  dataIndex: '',
  render:(text,record)=>(<Button className="noBorBtn" type="dashed" onClick={() => showDetail(record)}>用户查看</Button>)
}];

const data = [{
  key: 1,
  name:'艾文',
  alias:'艾文',
  descript:'艾文',
  user_group_name:'艾文',
},{
  key: 2,
  name:'刘丽',
  alias:'刘丽',
  descript:'刘丽',
  user_group_name:'刘丽',
},{
  key: 3,
  name:'毛利思',
  alias:'毛利思',
  descript:'毛利思',
  user_group_name:'毛利思',
},{
  key: 4,
  name:'范得离',
  alias:'范得离',
  descript:'范得离',
  user_group_name:'范得离',
},{
  key: 5,
  name:'余思瞳',
  alias:'余思瞳',
  descript:'余思瞳',
  user_group_name:'余思瞳',
},{
  key: 6,
  name:'于丽丽',
  alias:'于丽丽',
  descript:'于丽丽',
  user_group_name:'于丽丽',
},{
  key: 7,
  name:'王倩',
  alias:'王倩',
  descript:'王倩',
  user_group_name:'王倩',
},{
  key: 8,
  name:'李浩',
  alias:'李浩',
  descript:'李浩',
  user_group_name:'李浩',
},{
  key: 9,
  name:'兵山',
  alias:'兵山',
  descript:'兵山',
  user_group_name:'兵山',
},{
  key: 10,
  name:'王倩西',
  alias:'王倩西',
  descript:'王倩西',
  user_group_name:'王倩西',
}];
@Form.create()
export default class CPCompanyManage extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      tableLoading: false,
      selectedRowKeys: [],
      pageNo: 1,
      gameFormData:{},
      visible: false,
      userinfo:{}
    }
    // this.addData = this.addData.bind(this)
    // this.editData = this.editData.bind(this)
    // this.deletData = this.deletData.bind(this)
    this.onShowSizeChange = this.onShowSizeChange.bind(this)
    this.changePageNo = this.changePageNo.bind(this)
    this.showDetail = this.showDetail.bind(this)
  }
  componentDidMount() {
    this.props.form.validateFields();
  }
  showDetail=(val)=>{ //显示弹窗
    this.setState({
      visible: true,
      userinfo: val
    });
    console.log(this.state.userinfo)
  }

  changePageNo(page, pagesize) {// 用来监听pageno变化
    console.log(page)
    console.log(pagesize)
  }

  onShowSizeChange(current, pageSize) { // 用来监听pagesize变化
    console.log(current, pageSize);
  }

  handleSubmit = (e) => { // 查询
    e.preventDefault();
    console.log(e)
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render(){
    const {
      getFieldDecorator
    } = this.props.form;
    return(
      <div className="content fadein">
        <div>
          <div className="inputRight">
            <Form layout="inline" onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator('company', {})(
                  <Input placeholder="请输入合同名称" />
                )}
              </Form.Item>
              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                >
                  查询
                </Button>
              </Form.Item>
            </Form>
          </div>
            {/* <Button type="primary" style={{marginBottom:"20px"}} onClick={this.addData()} shape="round" icon="plus">添加</Button> */}
            <Table loading={this.state.tableLoading} pagination={false} className="nopadtable" columns={columns(this.showDetail)} dataSource={data} />,
            <div className="mb20" style={{ textAlign: "right" }}>
              <Pagination showSizeChanger onChange={this.changePageNo} onShowSizeChange={this.onShowSizeChange} defaultCurrent={this.state.pageNo} total={500} />
            </div>
          </div>

          <UserInfo visible={this.state.visible} userinfo={this.state.userinfo}/>
      </div>
    )
  }
}




